___CSS_LOADER_EXPORT___.push([module.id, "\n.text-input .link {\n\tcolor: #1a67a3 !important;\n\tfont-style: italic;\n}\n.text-input-copy-value * {\n\tcursor: copy !important;\n}\n", "",{"version":3,"sources":["webpack://./src/components/admin/TextInput.vue"],"names":[],"mappings":";AAuNA;CACA,yBAAA;CACA,kBAAA;AACA;AAEA;CACA,uBAAA;AACA","sourcesContent":["<template>\n\t<div class=\"text-input\">\n\t\t<div class=\"text-input-label\">\n\t\t\t{{ labelText }}\n\t\t</div>\n\t\t<div class=\"text-input-input-wrapper\">\n\t\t\t<input :id=\"id\"\n\t\t\t\tref=\"textInput\"\n\t\t\t\t:value=\"value\"\n\t\t\t\t:type=\"type\"\n\t\t\t\t:readonly=\"readOnly\"\n\t\t\t\t:class=\"{\n\t\t\t\t\t'text-input-error': !!errorMessage,\n\t\t\t\t\t'text-input-readonly': readOnly\n\t\t\t\t}\"\n\t\t\t\t:placeholder=\"placeHolder\"\n\t\t\t\t@click=\"$emit('click', $event)\"\n\t\t\t\t@input=\"$emit('input', $event.target.value)\"\n\t\t\t\t@change=\"$emit('change', $event.target.value)\"\n\t\t\t\t@focus=\"$emit('focus', $event)\"\n\t\t\t\t@blur=\"$emit('blur', $event)\">\n\t\t\t<Button v-if=\"showCopyButton\"\n\t\t\t\tclass=\"text-input-copy-value\"\n\t\t\t\t:disabled=\"isInputFieldEmpty\"\n\t\t\t\t:title=\"copyButtonTooltip\"\n\t\t\t\t@click=\"copyValue\">\n\t\t\t\t<template #icon>\n\t\t\t\t\t<ClippyIcon :size=\"16\" />\n\t\t\t\t</template>\n\t\t\t\t{{ t(\"integration_openproject\", \"Copy value\") }}\n\t\t\t</Button>\n\t\t</div>\n\t\t<div v-if=\"errorMessage || hintText\">\n\t\t\t<div v-if=\"errorMessage\" class=\"text-input-error-message\">\n\t\t\t\t{{ errorMessage }}\n\t\t\t</div>\n\n\t\t\t<div v-else\n\t\t\t\tclass=\"text-input-hint\"\n\t\t\t\tv-html=\"sanitizedHintText\" /> <!-- eslint-disable-line vue/no-v-html -->\n\t\t</div>\n\t</div>\n</template>\n<script>\nimport { translate as t } from '@nextcloud/l10n'\nimport { showSuccess } from '@nextcloud/dialogs'\nimport Button from '@nextcloud/vue/dist/Components/Button'\nimport ClippyIcon from '../icons/ClippyIcon'\nimport dompurify from 'dompurify'\n\nconst COPY_TIMEOUT = 5000\n\nexport default {\n\tname: 'TextInput',\n\tcomponents: {\n\t\tButton,\n\t\tClippyIcon,\n\t},\n\tprops: {\n\t\tvalue: {\n\t\t\tdefault: '',\n\t\t\ttype: String,\n\t\t},\n\t\tid: {\n\t\t\ttype: String,\n\t\t\trequired: true,\n\t\t},\n\t\ttype: {\n\t\t\ttype: String,\n\t\t\tdefault: 'text',\n\t\t},\n\t\tplaceHolder: {\n\t\t\tdefault: '',\n\t\t\ttype: String,\n\t\t},\n\t\tlabel: {\n\t\t\trequired: true,\n\t\t\ttype: String,\n\t\t},\n\t\thintText: {\n\t\t\tdefault: null,\n\t\t\ttype: [String, null],\n\t\t},\n\t\terrorMessage: {\n\t\t\tdefault: null,\n\t\t\ttype: [String, null],\n\t\t},\n\t\tisRequired: {\n\t\t\tdefault: false,\n\t\t\ttype: Boolean,\n\t\t},\n\t\twithCopyBtn: {\n\t\t\tdefault: false,\n\t\t\ttype: Boolean,\n\t\t},\n\t\treadOnly: {\n\t\t\tdefault: false,\n\t\t\ttype: Boolean,\n\t\t},\n\t},\n\tdata: () => ({\n\t\tisCopied: false,\n\t}),\n\tcomputed: {\n\t\tlabelText() {\n\t\t\tif (this.isRequired) {\n\t\t\t\treturn `${this.label} *`\n\t\t\t} else return this.label\n\t\t},\n\t\tisInputFieldEmpty() {\n\t\t\treturn !this.value\n\t\t},\n\t\tshowCopyButton() {\n\t\t\treturn (this.withCopyBtn && navigator.clipboard)\n\t\t},\n\t\tcopyButtonTooltip() {\n\t\t\tif (this.isCopied) {\n\t\t\t\treturn t('integration_openproject', 'Copied!')\n\t\t\t} else {\n\t\t\t\treturn t('integration_openproject', 'Copy value')\n\t\t\t}\n\t\t},\n\t\tsanitizedHintText() {\n\t\t\treturn dompurify.sanitize(this.hintText, { ADD_ATTR: ['target'] })\n\t\t},\n\t},\n\tmethods: {\n\t\tcopyValue() {\n\t\t\tconst that = this\n\t\t\tnavigator.clipboard.writeText(this.value)\n\t\t\tshowSuccess(t('integration_openproject', 'Copied to the clipboard'), {\n\t\t\t\ttimeout: COPY_TIMEOUT,\n\t\t\t})\n\t\t\tthat.isCopied = true\n\t\t\tsetTimeout(() => {\n\t\t\t\tthat.isCopied = false\n\t\t\t}, COPY_TIMEOUT)\n\t\t},\n\t},\n}\n</script>\n<style lang=\"scss\" scoped>\n.text-input {\n\t&-input-wrapper {\n\t\tdisplay: flex;\n\t\talign-items: center;\n\n\t\tinput {\n\t\t\tflex-